<template lang="html">
  <div class="person-center-page">
    <person-center-head :rightImg="newsIcon" :centerShow="false" :backShow="false" title=""></person-center-head>
    <div class="" class="wrapper">
      <person-center-info :userInfo="this.userInfo"></person-center-info>
      <person-center-coin :userInfo="this.userInfo"></person-center-coin>
      <person-center-partner></person-center-partner>
      <white-title @click.native="$router.push('/developfunction')" class="borderB" text="" :isShow="true" marginT=".3rem"></white-title>
      <white-title class="borderB" text="" :isShow="true" marginT=".3rem" @click.native="$router.push('/callnotes')"></white-title>
      <message-news :userInfo="this.userInfo"></message-news>
      <person-center-set marginT=".3rem"></person-center-set>
    </div>

    <tabbar selected="/personcenter"></tabbar>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import personCenterHead from '@/components/head/personCenterHead'
import whiteTitle from '@/components/common/whiteTitle'
import personCenterInfo from '@/components/personCenter/personCenterInfo'
import personCenterPartner from '@/components/personCenter/personCenterPartner'
import personCenterCoin from '@/components/personCenter/personCenterCoin'
import messageNews from '@/components/personCenter/messageNews'
import personCenterSet from '@/components/personCenter/personCenterSet'

import tabbar from '@/components/minCommon/tabbar'
import newsIcon from '@/assets/img/personcenter/news02@2x.png'

export default {
  components: { personCenterHead, personCenterInfo, personCenterPartner, personCenterCoin, tabbar, whiteTitle, messageNews, personCenterSet },
  data () {
    return {
      newsIcon: newsIcon,
      userInfo: {
        'uncommentCount': 0,
        'illegalCount': 0,
        'badCommentCount': 0,
        'adFeeCount': 0,
        'zhaoBiCount': 0,
        'isAuth': 0,
        'isBank': 0,
        'isPay': 0,
        'userInfo': {
          'mobile': ''
        }
      }
    }
  },
  computed: {
    isLogin: function () {
      return utils.isLogin()
    },
    userId: function () {
      return utils.userId()
    }
  },
  methods: {
    selectCircle (item) {
      this.title = item
    },
    getUserInfo () {
      if (this.userId) {
        api.getUserIndex({userId: this.userId}).then((response) => {
          let result = response.body
          if (result.isSuccess) {
            Object.keys(this.userInfo).forEach(k => {
              if (result.data[k]) {
                this.userInfo[k] = result.data[k]
              }
            })
            sessionStorage.setItem('userInfo', JSON.stringify(response.body.data))
          }
        })
      }
    }
  },
  mounted () {
    this.getUserInfo()
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';
.person-center-page {
  padding-top: .88rem;
  padding-bottom: 1.5rem;
}
</style>



