<template lang="html">
  <div class="message-news clearfix">
    <div class="message-news-item fl borderR" @click="$router.push('/waitingcomment')">
      <p class="title"></p>
      <p class="num">{{userInfo.uncommentCount || 0}}</p>
    </div>
    <div class="message-news-item fl borderR" @click="$router.push('/badcomment')">
      <p class="title"></p>
      <p class="num">{{userInfo.badCommentCount || 0}}</p>
    </div>
    <div class="message-news-item fl" @click="$router.push('/rulenotice')">
      <p class="title"></p>
      <p class="num">{{userInfo.illegalCount || 0}}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    userInfo: Object
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.message-news {
  height: 1.15rem;
  padding-top: .18rem;
  background: @whiteColor;
  .message-news-item {
    width: 33.33%;
    height: .78rem;
    line-height: .3rem;
    font-size: .3rem;
    text-align: center;
    .num {
      margin-top: .18rem;
      color: @messageColor;
    }
  }
}
</style>



