<template lang="html">
  <div class="person-center-coin clearfix">
    <div class="person-center-coin-item fl borderB borderR clearfix" @click="$router.push('/coinmanage')">
      <div class="icon fl">
        <img src="../../assets/img/personcenter/W01@2x.png" />
      </div>
      <div class="text fl">
        <p class="title"></p>
        <p class="detail clearfix">
          <span class="all-money fl"></span>
          <span class="all-number fl">{{userInfo.zhaoBiCount || 0}}</span>
          <span class="all-money fl"></span>
        </p>
      </div>
    </div>
    <div class="person-center-coin-item fl borderB clearfix" @click="toRateAdCash">
      <div class="icon fl">
        <img src="../../assets/img/personcenter/W02@2x.png" />
      </div>
      <div class="text fl">
        <p class="title"></p>
        <p class="detail clearfix">
          <span class="money fl"></span>
          <span class="rmb fl"></span>
          <span class="number fl">{{userInfo.adFeeCount || 0}}</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'

export default {
  props: {
    userInfo: Object
  },
  data () {
    return {
    }
  },
  computed: {
    isLogin: function () {
      return utils.isLogin()
    },
    userType () {
      return utils.getType()
    }
  },
  methods: {
    toRateAdCash () {
      if (!this.isLogin) {
        this.$router.push('/login')
        return
      }
      if (this.userInfo.isAuth !== 3) {
        let path = this.userType === 0 ? '/personrecognize' : 'companyrecognize'
        let message = this.userType === 0 ? '' : ''
        this.isRecognize(path, message)
        return
      }
      if (this.userInfo.isBank !== 1) {
        this.isBandcard('/bindbankcard')
        return
      }
      // if (this.userInfo.isPay !== 1) {-
      //   this.isSetPayPwd('/modifypayword')
      //   return
      // }
      this.adratecash()
    },
    adratecash () {
      var self = this
      minutils.MessageBoxConfirm({
        message: '',
        title: '',
        options: {
          confirmButtonText: '',
          cancelButtonText: ''
        },
        success (action) {
          self.$router.push({
            path: 'adratecash',
            query: {cash: self.userInfo.adFeeCount || 0, userName: encodeURI(self.userInfo.userInfo.realName || '')}
          })
        }
      })
    },
    isBandcard (path) {
      let self = this
      minutils.MessageBoxConfirm({
        message: '',
        options: {confirmButtonText: '', cancelButtonText: ''},
        success (action) {
          self.$router.push(path)
        }
      })
    },
    isRecognize (path, message) {
      let self = this
      minutils.MessageBoxConfirm({
        message: message + ', !',
        options: {confirmButtonText: '', cancelButtonText: ''},
        success (action) {
          self.$router.push(path)
        }
      })
    },
    isSetPayPwd (path) {
      let self = this
      minutils.MessageBoxConfirm({
        message: '',
        options: {confirmButtonText: '', cancelButtonText: ''},
        success (action) {
          self.$router.push(path)
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.person-center-coin {
  background: @whiteColor;
  .person-center-coin-item {
    width: 50%;
    height: 1.3rem;
    .icon {
      width: 1.34rem;
      height: 1.3rem;
      padding: .34rem 0 0 .56rem;
      img {
        width: .6rem;
        height: .6rem;
      }
    }
    .text {
      padding: .16rem 0 .17rem 0;
      .title {
        font-size: .34rem;
        line-height: .5rem;
      }
      .detail {
        font-size: .28rem;

        line-height: .46rem;
        color: @redColor;
        /**/
        .all-money {
          color: @defaultColor;
        }
        .all-number {
          margin: 0 .04rem;
        }
        /**/
        .money {
          color: @midleColor;
          width: .6rem;
        }
        .rmb {
          margin: 0 .08rem;
        }
        .number {
          min-width: .6rem;
        }
      }
    }
  }
}
</style>



