<template lang="html">
  <mt-tabbar v-model="selected">

    <mt-tab-item id="/search">
      <img v-if="selected === '/search'" slot="icon" src="../../assets/img/tabbar/l-icon01@3x.png">
      <img v-else slot="icon" src="../../assets/img/tabbar/l-icon02@3x.png">
      <span></span>
    </mt-tab-item>

    <mt-tab-item :id="logintype">
      <img v-if="selected === '/personcard' || selected === '/companycard'"
       slot="icon" src="../../assets/img/tabbar/m-icon01@3x.png">
      <img v-else slot="icon" src="../../assets/img/tabbar/m-icon02@3x.png">
      <span></span>
    </mt-tab-item>

    <mt-tab-item id="/circleindex">
      <img v-if="selected === '/circleindex'" slot="icon" src="../../assets/img/tabbar/q-icon01@3x.png">
      <img v-else slot="icon" src="../../assets/img/tabbar/q-icon02@3x.png">
      <span></span>
    </mt-tab-item>

    <mt-tab-item id="/personcenter">
      <img v-if="selected === '/personcenter'" slot="icon" src="../../assets/img/tabbar/w-icon01@3x.png">
      <img v-else slot="icon" src="../../assets/img/tabbar/w-icon02@3x.png">
      <span></span>
    </mt-tab-item>
  </mt-tabbar>
</template>

<script>
import utils from '@/utils/utils'
export default {
  props: {
    pathname: {
      type: String,
      default: '/search'
    },
    color: {
      type: String,
      default: '#2cc482'
    }
  },
  data () {
    return {
      selected: window.location.pathname
    }
  },
  watch: {
    selected (val) {
      this.$router.push(val)
    }
  },
  computed: {
    logintype () {
      let curUser = utils.hasUser()
      if (curUser && curUser.type === 0) {
        return '/personcard'
      } else {
        return '/companycard'
      }
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.mint-tabbar {
  height: 1.12rem;
  background: @whiteColor;
  position: fixed;
  z-index: 3;
  box-shadow: 0 0 .16rem rgba(0, 0, 0, .5);
}
.mint-tab-item-label span {
  font-size: .26rem;
}
.mint-tabbar > .mint-tab-item.is-selected {
  background: @whiteColor;
  color: @activeColor;
}
.mint-tab-item {
  padding: .14rem 0;
}
.mint-tab-item-icon {
  width: .48rem;
  height: .48rem;
  margin: 0 auto .1rem;
}
</style>



